@import url(https://fonts.googleapis.com/css2?family=Open+Sans:wght@300;400&display=swap);
.App {
  text-align: center;
}

.App-logo {
  height: 40vmin;
  pointer-events: none;
}

@media (prefers-reduced-motion: no-preference) {
  .App-logo {
    -webkit-animation: App-logo-spin infinite 20s linear;
            animation: App-logo-spin infinite 20s linear;
  }
}

.App-header {
  background-color: #282c34;
  min-height: 100vh;
  display: -webkit-flex;
  display: flex;
  -webkit-flex-direction: column;
          flex-direction: column;
  -webkit-align-items: center;
          align-items: center;
  -webkit-justify-content: center;
          justify-content: center;
  font-size: calc(10px + 2vmin);
  color: white;
}

.App-link {
  color: #61dafb;
}

@-webkit-keyframes App-logo-spin {
  from {
    -webkit-transform: rotate(0deg);
            transform: rotate(0deg);
  }
  to {
    -webkit-transform: rotate(360deg);
            transform: rotate(360deg);
  }
}

@keyframes App-logo-spin {
  from {
    -webkit-transform: rotate(0deg);
            transform: rotate(0deg);
  }
  to {
    -webkit-transform: rotate(360deg);
            transform: rotate(360deg);
  }
}
/***** Navbar *****/ 
nav {
  z-index: 2;
}
.logo {
  width: 6rem;
  height: 6rem;
  margin-left: 0;
}
:root {
  --primary-white: #fff;
  --primary-light-grey: #f4f4f4;
  --primary-grey: #ccd1d9;
  --primary-dark-grey: #666666;
  --primary-green: #71d385;
  --primary-dark: #343a40;
  --primary-red: #ef4035;
  --primary-hover-red: #f33501;
  --primary-black: #000000;
}


body {
  font-family: 'Open Sans', sans-serif !important;
}
/***** Menu Links *****/
nav a {
  text-decoration: none !important;
}

nav a.nav-link {
  letter-spacing: 0.0375rem;
  cursor: pointer;
}

nav ul li {
  text-transform: uppercase;
}

.navbar-nav > li > a {
  margin-right: 0.75rem;
  margin-left: 0.75rem;
  color: #fff !important;
  color: var(--primary-white) !important;
  border-top: 0.1875rem solid #343a40;
  border-top: 0.1875rem solid var(--primary-dark);
}

.navbar-brand {
  min-width: 17.25rem;
  padding: 0 !important;
  height: 6rem !important;
}

@media(max-width: 768px) {
  .navbar-brand {
    width: 7rem;
  }
}

.navbar-nav > li > a.active, .navbar-nav > li > a:focus {
  border-top: .1875rem solid #71d385;
  border-top: .1875rem solid var(--primary-green);
}


.navbar-nav > li > a:hover {
  border-top: .1875rem solid #71d385;
  border-top: .1875rem solid var(--primary-green);
}

/***** Header *****/

.header-wrapper {
  position: relative;
  background: url(/react-portfolio/static/media/laptop.20e9d85a.jpg) no-repeat;
  background-size: cover;
  background-position: center;
  height: 100vh;
}

.main-info {
  display: -webkit-flex;
  display: flex;
  position: absolute;
  top: 0;
  left: 0;
  right: 0;
  bottom: 0;
  -webkit-flex-direction: column;
          flex-direction: column;
  -webkit-justify-content: center;
          justify-content: center;
  -webkit-align-items: center;
          align-items: center;
  text-align: center;
  font-family: 'Open Sans', sans-serif;
  z-index: 1;
}

.main-info h1 {
  color: #fff;
  color: var(--primary-white);
  text-transform: uppercase;
  text-shadow: 2px 2px #000000;
}

@media(max-width: 768px) {
  .main-info h1 {
    font-size: 1.5rem !important;
  }
}

/***** Typed Text ***********/
.typed-text {
  font-size: 2rem;
  color: #fff;
  color: var(--primary-white);
  text-shadow: 2px 2px #000000;
}

@media(max-width: 768px) {
  .main-info h1 {
    font-size: 1.7rem !important;
  }
  .typed-text {
    font-size: 1.3rem;
  }
}

/***** Header main offer btn **********/
.btn-main-offer {
  border: 1px solid #71d385;
  border: 1px solid var(--primary-green);
  text-transform: uppercase;
  border-radius: 0;
  padding: 0.625rem 0.75rem 0.75rem 0.75rem;
  color: #fff;
  color: var(--primary-white);
  margin: 2rem;
}

.btn-main-offer:hover {
  text-decoration: none;
  background-color: #71d385;
  background-color: var(--primary-green);
  color: #fff;
  color: var(--primary-white);
  transition: .2s ease-in-out;
}

/***** About Me ******/
.about-me-wrapper {
  background: #f4f4f4;
  background: var(--primary-light-grey);
}
.photo-wrap {
  display: -webkit-flex;
  display: flex;
  -webkit-justify-content: center;
          justify-content: center;
  -webkit-align-items: center;
          align-items: center;

}
.profile-img {
  width: 16rem;
  border-radius: 0;
  padding: 0.1rem;
  border: 2px solid #71d385;
  border: 2px solid var(--primary-green)
}

.about-heading {
  color: #71d385;
  color: var(--primary-green);
}

@media(max-width: 468px) {
  .profile-img{
    width: 10rem;

  }
}

/***** Particles *******/
.tsparticles-canvas-el {
  position: absolute;
  height: 100vh;
  width: 100vw;
  z-index: 1;
} 

@media(max-width: 768px) {
  .particles-canvas {
    display: none;
  }
}

/***** Resume *******/

.resume-wrapper {
  position: relative;
  padding: 3.125rem;
  overflow: hidden;
}

.resume h1 {
  color: #71d385;
  color: var(--primary-green);
  padding: 0.7rem;
}

.resume-wrapper::before {
  position: absolute;
  content: "";
  top: 0;
  left: 50%;
  margin-left: -0.0625rem;
  width: 0.125rem;
  height: 100%;
  background: #ccd1d9;
  background: var(--primary-grey);
  z-index: 1;
}

.timeline-block {
  width: calc(50% + 0.5rem);
  display: -webkit-flex;
  display: flex;
  -webkit-justify-content: space-between;
          justify-content: space-between;
  clear: both;
}

.timeline-block-right {
  float: right;
}

.timeline-block-left {
  float: left;
  direction: rtl;
}

.marker {
  width: 1rem;
  height: 1rem;
  border-radius: 50%;
  border: 0.3125rem solid #666666;
  border: 0.3125rem solid var(--primary-dark-grey);
  background: #71d385;
  background: var(--primary-green);
  margin-top: 0.625rem;
  z-index: 3;
}

.timeline-content {
  width: 95%;
  padding: 0 0.9375rem;
  color: #666666;
  color: var(--primary-dark-grey);
  direction: ltr;
}

.timeline-content h3 {
  margin-top: 0.3125rem;
  font-weight: 900;
}

/***** Media *******/

@media(max-width: 768px) {
  .resume-wrapper .container {
    padding-left: 0;
  }
  .resume-wrapper::before {
    left: 0.5rem;
    margin-bottom:  1.875em;
  }
  .timeline-block-right {
    float: none;
    width: 100%;
  }
  .timeline-block-left {
    float: none;
    direction: ltr;
  }
}

/***** Portfolio *******/

.portfolio-wrapper {
  background: #f4f4f4;
  background: var(--primary-light-grey);
  padding: 3rem 0;
}

.portfolio-wrapper h1 {
  color: #71d385;
  color: var(--primary-green);
}

.portfolio-image {
  width: 15rem;
  border: 1px solid #71d385;
  border: 1px solid var(--primary-green);
  padding:  0.5rem;
}

.portfolio-image-box {
  position: relative;
  margin: 1rem;
}

/***** Overflow box *******/

.overflow {
  position: absolute;
  opacity: 0;
  background: #000000;
  background: var(--primary-black);
  width: 15rem;
  height: 12.5rem;
  top: 0;
  border: 1px solid #ef4035;
  border: 1px solid var(--primary-red);
  cursor: pointer;
}

.portfolio-image-box:hover .overflow {
  opacity: 60%;
  transition: 0.3s ease-in-out;
}

.portfolio-icon {
  position: absolute;
  opacity: 0;
  margin-left: -8rem;
  margin-top: 5.2rem;
  color: #71d385;
  color: var(--primary-green);
  font-size: 1.5em;
}

.portfolio-image-box:hover .portfolio-icon {
  opacity: 70%;
  transition: 0.3s ease-in-out;
  cursor: pointer;
}

.popupbox-wrapper {
  margin-top: 15rem;
  height: 100%;
  width: 100%;
  overflow: auto;
}

/***** Popupbox *******/

.portfolio-image-popupbox {
  width: 45rem;
  padding: 0 0.5rem;
  /* overflow: auto; */
}

.hyper-link {
  cursor: pointer;
  color: #71d385;
  color: var(--primary-green)
}
.hyper-link:hover {
  cursor: pointer;
  color: #666666;
  color: var(--primary-dark-grey)
}

@media(max-width: 768px) {
  .portfolio-image-popupbox {
    width: 100%;
  }
}

@media(max-height: 380px) {
  .popupbox-wrapper {
    height: 90% !important;

  }
  .portfolio-image-popupbox {
    width: 10%;
  }
}

/***** ContactMe *******/

.contactme {
  background: #343a40;
  background: var(--primary-dark);
  padding: 3rem 0;
}

.contactme h1 {
  color: #71d385;
  color: var(--primary-green);
  padding: 1.3rem 0;
}

.contactme p {
  color: #f4f4f4;
  color: var(--primary-light-grey);
  font-size: 1.2rem;
}

/***** inputs *******/

.contactme input, .contactme textarea {
  width: 100%;
  box-sizing: border-box;
  background: none;
  resize: none;
  border: 0;
  border-bottom: 0.125rem solid #666666;
  border-bottom: 0.125rem solid var(--primary-dark-grey);
  border-radius: 0;
  color: #ccd1d9;
  color: var(--primary-grey);
  outline: 0 !important;
  box-shadow: none !important;
  margin-bottom: 1rem;
  padding-left: 0 !important;
}

.contactme .container {
  padding: 4rem 0;
}

.contactme input:focus, .contactme textarea:focus {
  background: transparent;
  color: #71d385;
  color: var(--primary-green);
  border-bottom: 0.125rem solid #666666;
  border-bottom: 0.125rem solid var(--primary-dark-grey);
}

.contactme textarea {
  height: 12.2rem !important;
}

.contact-btn {
  margin: 1rem 0 0 0;
  width: 100%;
  background: none;
}

.line {
  position: relative;
  top: -0.8rem;
  width: 0;
  height: 0.125rem;
  background: #71d385;
  background: var(--primary-green);
  display: inline-block;
}

input:focus+.line, textarea:focus+.line {
  width: 100%;
  transition: width 0.5s ease-in-out;
}

input.form-control {
  margin: -0.3rem 0;
}

textarea.form-control {
  margin: -0.3rem 0;
}

.success-message {
  font-weight: 900;
  color: #71d385;
  color: var(--primary-green);
}

@media(max-width: 768px) {
  .contactme {
    overflow-x: hidden;
  }
  .contactme .container {
    padding: 0 1rem;
  }

  .contactme p {
    padding: 0 0.04rem;
  }
}

/***** Footer *******/

.footer {
  background: #000000;
  background: var(--primary-black);
  color: #ccd1d9;
  color: var(--primary-grey);
  padding: 3rem;
}

.footer a {
  text-decoration: none;
  color: #666666;
  color: var(--primary-dark-grey);
}

.footer a:hover {
  text-decoration: none;
  color: #71d385;
  color: var(--primary-green);
  cursor: pointer;
}

.footer p {
  margin-bottom: 0;
}
